const generateGammaMajor = (mainNote) => {
    const arr = [[mainNote]];
    const majorDiffs = [2, 2, 1, 2, 2, 2, 1];
    majorDiffs.forEach((v) => {
        const lastNote = arr[arr.length - 1][0];
        arr.push([lastNote + v]);
    });

    const arrUp = arr.slice();
    const arrDown = arr.reverse();

    return [...arrUp, [], ...arrDown, [], []];
};

const generateGammaMinor = (mainNote) => {
    const arr = [[mainNote]];
    const minorDiffs = [2, 1, 2, 2, 1, 2, 2];
    minorDiffs.forEach((v) => {
        const lastNote = arr[arr.length - 1][0];
        arr.push([lastNote + v]);
    });

    const arrUp = arr.slice();
    const arrDown = arr.reverse();

    return [...arrUp, [], ...arrDown, [], []];
};

export default {
    'CMajor': generateGammaMajor(60),
    'CMinor': generateGammaMinor(60),
    'DMajor': generateGammaMajor(62),
    'DMinor': generateGammaMinor(62),
    'EMajor': generateGammaMajor(64),
    'EMinor': generateGammaMinor(64),
    'FMajor': generateGammaMajor(65),
    'FMinor': generateGammaMinor(65),
    'GMajor': generateGammaMajor(67),
    'GMinor': generateGammaMinor(67),
    'AMajor': generateGammaMajor(69),
    'AMinor': generateGammaMinor(69),
    'BMajor': generateGammaMajor(71),
    'BMinor': generateGammaMinor(71)
};



